@import url(https://fonts.googleapis.com/css2?family=Ubuntu);
/* Auth form */
.auth__form-container {
  min-height: 100vh;
  display: flex;
  flex-direction: row;
}

.auth__form-container_fields {
  flex: 2 1;
  display: flex;
  flex-direction: column;
  justify-content: center;

  padding: 2rem;
  background: #005fff;
}

.auth__form-container_image {
  flex: 3 1;
  display: flex;
  box-shadow: 1px 0px 5px rgba(0, 0, 0, 0.05);
}

.auth__form-container_image img {
  width: 100%;
  height: 100%;
}

.auth__form-container_fields-content {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  padding: 2rem;
  box-shadow: 0px 1px 5px rgb(0 0 0 / 10%);
  border-radius: 5px;
  transition: 0.8s ease;
  background: #fff;
}

.auth__form-container_fields-content p {
  font-size: 1.5rem;
  font-family: Arial, Helvetica, sans-serif;
  color: #05245a;
  font-weight: 900;
}

.auth__form-container_fields-content_input {
  display: flex;
  flex-direction: column;
  position: relative;

  margin: 1rem 0rem;
}

.auth__form-container_fields-content_input label {
  margin-bottom: 0.45rem;
  color: rgb(61, 79, 88);
  font-size: 12px;
  font-family: Arial, Helvetica, sans-serif;
  letter-spacing: 0.7px;
  line-height: 1.3;
}

.auth__form-container_fields-content_input input {
  padding: 0.55rem 0.4rem;
  border: 1px solid rgb(184, 196, 194);
  border-radius: 4px;
  font-size: 14px;
  outline: none;
  transition: all 150ms ease-in-out 0s;
  width: 85%;
  background: #fff;
}

.auth__form-container_fields-content_input input::-webkit-input-placeholder {
  color: #b1b1b1;
  width: 100%;
  font-weight: unset;
  font-family: Arial, Helvetica, sans-serif;
}

.auth__form-container_fields-content_input input:-ms-input-placeholder {
  color: #b1b1b1;
  width: 100%;
  font-weight: unset;
  font-family: Arial, Helvetica, sans-serif;
}

.auth__form-container_fields-content_input input::placeholder {
  color: #b1b1b1;
  width: 100%;
  font-weight: unset;
  font-family: Arial, Helvetica, sans-serif;
}

.auth__form-container_fields-content_input input:hover {
  border-color: #dcdddd;
}

.auth__form-container_fields-content_input input:focus,
.auth__form-container_fields-content_input input:active {
  box-shadow: 0px 0px 0px 1.5px #005fff;
  border-color: #005fff;
}

.auth__form-container_fields-content_input-password {
  position: absolute;
  right: 13%;
  top: 50%;
  cursor: pointer;
}

.auth__form-container_fields-content_button {
  margin-top: 2rem;
  display: flex;
  justify-content: flex-start;
}

.auth__form-container_fields-content_button button {
  border-radius: 4px;
  background: #005fff;
  border: 1px solid #005fff;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 500;
  padding: 0.7rem 1.2rem;
  outline: none;
  cursor: pointer;
  transition: 0.3s ease;
}

.auth__form-container_fields-content_button button:hover {
  background: #0066ff;
}

.auth__form-container_fields-account {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  margin-top: 0.2rem;
}

.auth__form-container_fields-account p {
  font-size: 14px;
  color: #000;
  font-weight: 500;
}

.auth__form-container_fields-account span {
  color: #05245a;
  cursor: pointer;
  font-weight: 700;
}

@media screen and (max-width: 800px) {
  .auth__form-container {
    flex-direction: column-reverse;
  }

  .auth__form-container_fields {
    justify-content: flex-start;
  }

  .auth__form-container_image {
    height: 150px;
    flex: none;
    box-shadow: none;
  }

  .auth__form-container_image img {
    object-fit: cover;
  }
}

@media screen and (max-width: 375px) {
  .auth__form-container_fields {
    padding: 2rem 0.5rem;
  }

  .auth__form-container_fields-content_input input {
    width: 95%;
  }

  .auth__form-container_fields-content_input-password {
    right: 3%;
  }
}

* {
  box-sizing: border-box;
}

/* Header component */
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #1a3041;
  height: 10vh;
  padding: 10px 20px;
  margin-bottom: 10px;
}

.home-link {
  padding: 5px 10px;
  border: none;
  text-decoration: none;
  border-radius: 5px;
  background-color: #66829e;
  color: #fff;
  cursor: pointer;
  transition: 0.3s;
}

.home-link:hover {
  background-color: #fff;
  color: #000;
}

.home-body {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 100vh;
  margin: 0;
}

.home-button {
  font-size: 20px;
  font-weight: 300;
  padding: 10px 15px;
  margin: 25px;
  border-radius: 15px;
  text-decoration: none;
  text-align: center;
  background-color: #4eb5f1;
  box-shadow: 2px 3px #8888;
  color: #fff;
  cursor: pointer;
  outline: none;
  transition: all 0.2s;
}

.home-button:hover {
  background-color: #4095c6;
}

@media all and (max-width: 992px) {
  .home-body {
    display: flex;
    flex-direction: column;
    /* margin: 0 4px; */
  }
  .home-button {
    font-size: 16px;
    width: 200px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.slot__app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 25%;
}

.slot__app-containers {
  position: relative;
  display: flex;
  flex-direction: row;
  width: 600px;
  margin-top: 50px;
}

.slot__app-section {
  position: relative;
  border-radius: 10px !important;
  border: 3px solid black !important;
  width: 100%;
  height: 175px;
  overflow: hidden;
  background-color: #a3c9d6;
  border-radius: 3px;
  border: 1px solid lightgrey;
  text-align: center;
  font-size: 85px;
  line-height: 150px;
  cursor: default;
  margin-bottom: 20px;
}

.slot__app-single-container {
  position: relative;
  top: 2px;
  width: 100%;
  transition: top ease-in-out 0.3s;
  text-align: center;
  align-items: baseline;
}

#slot__app-span {
  font-size: 60px;
  padding-bottom: 20px;
  margin-top: 40px;
}

.roll {
  width: 200px;
  cursor: pointer;
  background-color: #008cba;
  text-align: center;
  align-items: center;
  padding: 10px;
  text-align: center;
  font-size: 25px;
  border-radius: 10px;
  border: 1px solid black;
}

.rolling {
  animation: blinkingText 1.5s infinite;
}

.slot__app-info {
  margin: 10px;
  font-size: 20px;
  color: #fff;
}

.slot__app-info .slot__app-result-span {
  font-size: 20px;
  font-weight: 400;
}

table {
  border: 1px solid grey;
  border-radius: 3px;
  padding: 10px;
  text-align: center;
}

@keyframes blinkingText {
  0% {
    color: #000;
  }
  49% {
    color: #000;
  }
  60% {
    color: transparent;
  }
  99% {
    color: transparent;
  }
  100% {
    color: #000;
  }
}

@media all and (max-width: 700px) {
  .slot__app-containers {
    width: 330px;
  }
  .slot__app-section {
    height: 150px;
    line-height: 135px;
  }
  .roll {
    width: 160px;
    height: 50px;
    font-size: 20px;
  }
  .slot__app-info {
    font-size: 16px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Ubuntu", sans-serif;
  background-color: #303e47;
}

a {
  text-decoration: none;
}

.buttons {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.btn {
  background-color: var(--dark-bg-color);
  padding: 5px;
  cursor: pointer;
  text-decoration: none;
  color: #fff;
  border: 2px solid var(--dark-bg-color);
  border-radius: 4px;
  transition: background-color 0.2s;
}

/* filter component */

.filter__form input {
  height: 50px;
  width: 400px;
  font-size: 20px;
  padding-left: 10px;
  margin-left: 20px;
}

/* Loading */
.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-top: 5rem;
}

/* component */

/* Countries component */
.countries__list {
  display: grid;
  grid-template-columns: 1fr;
  padding: 20px;
  grid-gap: 20px;
  gap: 20px;
  max-width: fit-contendefault;
}

.countries__list a {
  cursor: default;
}

.countries__list article {
  box-shadow: 0 10px 10px rgba(42, 65, 80, 0.5);
}

.countries__list-flag img {
  border-radius: 5px 5px 0 0;
  width: 400px;
  height: 150px;
  object-fit: cover;
}

.countries__list-details {
  background-color: #66829e;
  color: #fff;
  text-decoration: none;
  padding: 20px;
  margin-top: -10px;
  border-radius: 0 0 5px 5px;
}

.countries__list-details h4 {
  color: #d4c9c9;
  padding-bottom: 5px;
  margin: 10px 0;
}

.countries__list-details h4 span {
  color: #fff;
  font-weight: 500;
}

/* Media queries */
@media (min-width: 640px) {
  .countries__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
    gap: 40px;
  }

  .countries__list .countries__list-details {
    margin: -10px 0 0;
  }
}

@media (min-width: 992px) {
  .countries__list {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 1366px) {
  .countries__list {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (max-width: 750px) {
  .filter-form input {
    width: 250px;
    height: 40px;
    font-size: 16px;
  }
}

.list-table {
  border-collapse: collapse;
}

.list-table td,
th {
  border: 1px solid #999;
  padding: 0.5rem;
  text-align: left;
}

.list-table tbody > tr > td > img {
  width: 100px;
  height: 80px;
}

/* *,
::after,
::before {
  box-sizing: border-box;
} */

.form {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: 40px 50px;
}

.form input,
.form select {
  background-color: #fff;
  padding: 12px;
  font-family: inherit;
  border: none;
  border-radius: 5px;
  color: var(--text-color);
}

img {
  max-width: 100%;
}

.country__search-input {
  padding: 10px;
  font-size: 17px;
  border: 1px solid grey;
  float: left;
  width: 30%;
  background: #f1f1f1;
}

.country__search-button {
  float: left;
  width: 10%;
  padding: 10px;
  background: #2196f3;
  color: white;
  font-size: 17px;
  border: 1px solid grey;
  border-left: none;
  cursor: pointer;
}

.search-button:hover {
  background: #0b7dda;
}

.country__list {
  padding: 50px;
  color: #f1f1f1;
}

.btn-light {
  margin-bottom: 30px;
  display: inline-block;
}

.country__list article .country__list-details div {
  margin: 30px 0;
}

.country__list article .country__list-details div h2 {
  font-size: 30px;
  letter-spacing: 1px;
}

.country__list article .country__list-details div h5 {
  font-weight: 600;
  font-size: 18px;
  margin: 10px 0;
}

.country__list article .country__list-details div h5 span {
  font-weight: 300;
}

.country__list article h3 {
  font-weight: 600;
}

.country__list article .country__list-borders {
  display: flex;
  flex-wrap: wrap;
}

.country__list article .country__list-borders ul {
  margin: 10px;
  background-color: var(--article-color);
  padding: 3px 20px;
  border-radius: 3px;
  box-shadow: 0 5px 20px hsla(200, 15%, 8%, 0.5);
}

.country__list article .country__list-borders ul.light-theme {
  background-color: var(--light-mode-background);
}

@media (min-width: 992px) {
  .country__list article .country__list-inner {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
    gap: 40px;
  }

  .country__list .country__list-details {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    gap: 20px;
  }
}

@media (min-width: 1200px) {
  .country__list {
    max-width: 1100px;
    margin: auto;
  }
}

@media (min-width: 1366px) {
  .country__list {
    max-width: 1200px;
  }
}

@media (min-width: 1440px) {
  .country__list {
    max-width: 1400px;
  }
}

@media (max-width: 992px) {
  body {
    margin: 5px;
  }
  .country__search-input {
    width: 75%;
  }
  .country__search-button {
    width: 25%;
  }
}

/* Array list styles */

.countries-list {
  display: flex;
  flex-direction: column;
  color: #fff;
  border-collapse: collapse;
  margin-top: -10px;
  margin-left: 20px;
  margin-right: 20px;
  overflow-x: hidden;
}

th {
  text-align: center;
}

td {
  font-size: 16px;
}

@media (max-width: 700px) {
  th {
    padding: 0;
    border: none;
    font-size: 10px;
  }
  td {
    font-size: 10px;
  }
  td img {
    width: 15px;
    height: 10px;
  }
}

